html {
    overflow-y: scroll;
    width: 640px;
    margin: auto;
}

body {
    font-family: monospace, monospace;
    font-size: 18px;
    background: #fcfcfc;
    color: black;
}

hr {
    margin: 12px 0px;
    border: 1px solid #d0e0d1;
}

header {
    font-size: 20px;
    line-height: 40px;
    & > nav {
        float: right;
    margin-left: 20px;
    font-size: 16px;
    color: #97bd99;
    }
}

footer {
    font-size: 14px;
    text-align: center;
}

main {
    margin: 30px 0px;
    &#homepage {
        text-align: center;
    }
    &.regular {
        font-size: 16px;
    }
}

@media screen and (max-width: 700px) {
    html {
        width: 90vw;
    }
}

/* p, ul, ol {
    margin: 0px 0px 24px;
} */

a {
    color: #97bd99;
    text-decoration: none;
    &:hover, .current {
        font-weight: bold;
    }
}

.green {
    color: #97bd99;
}

h1 {
    color: black;
    font-size: 20px;
    line-height: 36px;
    margin: 24px 0;
    text-align: center;
}
